.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  color: white;
}

.Header-links{
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-align: center;
      align-items: center;
}


.navbar {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  background: #222;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: row;
      flex-direction: row;
}


.navbar li a,
.navbar .dropbtn {
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 5px 10px;
  display:block;
}

.navbar li a:hover,
.navbar .dropdown:hover .dropbtn{
  background: blue;
}

.dropdown {

  display:inline-block;
}

.dropdown-content {
  background: #000000;
  position: absolute;
  display: none;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align:left;
}

.dropdown-content a:hover {
background: #bada55;

}

.dropdown:hover .dropdown-content {
  display: block;
}

.show & .profile_content{
  display: block;
}
.profile_card{
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.profile_content{
  display: none;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  top: 42px;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #fff;
    margin: 6px 0;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

@media screen and (max-width: 600px) {
  .navbar {
    width: 100%;
    position: relative;
    height: auto;
  }
}

@media screen and (max-width: 400px) {
  .navbar li,{
    float: none;
    text-align: center;
  }
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



